function getCategoryIcon(category) {
  switch (category) {
    case 1: return C1();
    case 2: return C2();
    case 3: return C3();
    case 4: return C4();
    case 5: return C5();
    case 6: return C6();
    case 7: return C7();
    case 8: return C8();
    case 9: return C9();
    default: return C0();
  }
}

const blueTheme = createTheme({
  palette: {
    primary: {
      light: '#8CC2FF',
      main: '#8CC2FF',
      dark: '#8CC2FF',
      contrastText: '#fff',
    },
    secondary: {
      light: '#FF9999',
      main: '#FF9999',
      dark: '#FF9999',
      contrastText: '#000',
    },
  },
});

const redTheme = createTheme({
  palette: {
    primary: {
      light: '#FF9999',
      main: '#FF9999',
      dark: '#FF9999',
      contrastText: '#000',
    },
    secondary: {
      light: '#8CC2FF',
      main: '#8CC2FF',
      dark: '#8CC2FF',
      contrastText: '#fff',
    },
  },
});

const grayTheme = createTheme({
  palette: {
    primary: {
      light: '#AAAAAA',
      main: '#AAAAAA',
      dark: '#AAAAAA',
      contrastText: '#000',
    },
    secondary: {
      light: '#8CC2FF',
      main: '#8CC2FF',
      dark: '#8CC2FF',
      contrastText: '#fff',
    },
  },
});

// Delay function for anything. Incredibly useful.
function delay(ms) {
  return new Promise((resolve) => {
      setTimeout(resolve, ms);
  })
 }

class App extends React.Component {
  constructor(props) {
    super(props);
    this.firebase = new Firebase();
    this.state = {tab: 0, splashSize: 20.0, canContinue: false, online: true, promptRetry: false, openedItem: undefined, sharedItem: undefined, hashIsPost: false, hashIsShare: false, promptSignIn: false, createPost: 2, installPrompt: false};

    this.animator();
    this.share = this.share.bind(this);
    this.clearPosts = this.clearPosts.bind(this);
    this.onlineOrNot = this.onlineOrNot.bind(this);
    this.changeShowItem = this.changeShowItem.bind(this);
    this.handleHashChange = this.handleHashChange.bind(this);
    this.handleAppPromotion = this.handleAppPromotion.bind(this);
    
    const auth = getAuth();
    onAuthStateChanged(auth, (user) => {
      if (user) {
        this.setState({signedIn: true, user: auth.currentUser, donations: []});
        this.retrievePosts(user);
      } else {
        this.setState({signedIn: false});
        this.setState({donations: [{district:'96000 Sibu, Sarawak', category:'Cooked food • Ready-to-eat food', d:"Please spread the word about Bersamamu 📢, so more people are able to benefit from it 👍. Thank you so much! 🙏", t:1646316215310, c:1}, {district:'50088 Kuala Lumpur', category:'Furniture • Home Items', d:"Hello! I am an example of Donation. You can see more Donations and Requests after signing in 📲!", t:1646016215310, c:9}]});
      }
    })
  }

  componentDidMount() {window.addEventListener('hashchange', this.handleHashChange); window.addEventListener('beforeinstallprompt', (e)=>{e.preventDefault(); console.log("seggs", e); this.setState({installPrompt: e})}); this.handleHashChange();}
  componentWillUnmount() {window.removeEventListener('hashchange', this.handleHashChange);}

  // Handle PWA promotion.
  handleAppPromotion() {
    this.state.installPrompt.prompt();
    this.setState({installPrompt:false});
  }
  // Handle hash changes! VERY IMPORTANT.
  handleHashChange() {
    var isPost = window.location.hash==="#post";
    var isShare = (window.location.hash.length>7 && window.location.hash.substring(0,7)=="#share=");
    this.setState({hashIsPost: isPost, hashIsShare: isShare})
    
    if (isPost) {
      if (this.state.openedItem==undefined) this.changeShowItem() 
    }
    else if (isShare) {
      this.setState({sharedItem: window.location.hash.substring(7)});
      if (!this.state.signedIn) this.retrievePosts(false);
    }
    else if (window.location.hash!="#") this.changeShowItem(undefined);
  }
  // Retrieve all items.
  retrievePosts(user=undefined) {
    this.firebase.obtainDonations(this.onlineOrNot).then(items => {this.setState({donations: items}); if (this.state.hashIsShare&&this.state.sharedItem.substring(0,1)=='d') this.shareItemCheck(items)});
    this.firebase.obtainRequests(this.onlineOrNot).then(items => {this.setState({requests: items}); if (this.state.hashIsShare&&this.state.sharedItem.substring(0,1)=='r') this.shareItemCheck(items)});
    if (user) this.firebase.obtainUserPosts(user.uid, this.onlineOrNot).then(items => {this.setState({userposts: items})});
  }
  // Clear all items after signout.
  clearPosts() {
    this.firebase.signOut();
    this.setState({donations:[], requests:[], userposts:[], user:[], tab:0});
  }
  // Check for shared items in array.
  shareItemCheck(items) {
    items.forEach(item => {if (item['key']==this.state.sharedItem.substring(1)) this.setState({openedItem: item})})
  }

  // Should we show the item page?
  shouldShowItem() {
    return (this.state.hashIsPost||this.state.hashIsShare) && this.state.openedItem;
  }
  // Add/Remove focused item.
  changeShowItem(item) {
    if (item===undefined) { this.setState({openedItem: undefined}); window.location.assign('#'); if (!this.state.signedIn)this.setState({donations: [{district:'96000 Sibu, Sarawak', category:'Cooked food • Ready-to-eat food', d:"Please spread the word about Bersamamu 📢, so more people are able to benefit from it 👍. Thank you so much! 🙏", t:1646316215310, c:1}, {district:'50088 Kuala Lumpur', category:'Furniture • Home Items', d:"Hello! I am an example of Donation. You can see more Donations and Requests after signing in 📲!", t:1646016215310, c:9}]});
    } else { this.setState({openedItem: item}); window.location.assign("#post") }
  }

  // Creates share URL and opens share sheet.
  share() {
    var link = "https://jing-yen.github.io/bersamamu-web/#share=".concat(this.state.openedItem["isdonation"]?"d":"r", this.state.openedItem["key"]);
    if (navigator.share) navigator.share({
      title: 'Bersamamu Web',
      text: "Sharing a "+this.state.openedItem["isdonation"]?"donation":"request"+" from Bersamamu",
      url: link
    })
      .then(()=>console.log("Share successful."))
      .catch((error)=>console.log(error));
    else { navigator.clipboard.writeText(link); alert("Link copied to clipboard :)") }
  }

  // Returns the appropriate theme.
  getTabSpecific(obj) {
    if (this.state.tab==0) { if (obj.theme) return blueTheme; else if (obj.title) return "Donations"; else return Donations() } 
    else if (this.state.tab==1) { if (obj.theme) return redTheme; else if (obj.title) return "Requests"; else return Requests() }
    else { if (obj.theme) return grayTheme; else if (obj.title) return "My Account"; else return Me() }
  }

  // Just toggles the state of 'online' variable.
  async onlineOrNot(isOnline) {
    this.setState({online: isOnline, promptRetry: false});
    await delay(5000);
    if (!this.state.online) this.setState({promptRetry: true});
  }

  // Animates the growing and shrinking of logo during splashscreen.
  async animator() {
    var growing = false, delta = 0.0;
    this.bouncer(); await delay(250);
    while (true) {
      await delay(5); 
      if (this.state.splashSize>21 || this.state.splashSize<19) { growing = !growing; if (!growing) await delay(1500); }
      if (growing) delta = 0.1; else delta = -0.1;
      this.setState({splashSize: this.state.splashSize+delta});
    }
  }
  
  // Bouncer cuz it refuses entry for 0.8s, haha.
  async bouncer() {
    await delay(800); this.setState({canContinue: true});
  }

  // Here is where        o      o
  // our rendering           L
  // functions start!    \_|_|_|_/

  HomeBody() {
    return (
      <ThemeProvider theme={this.getTabSpecific({theme:true})}>
        {this.SignInSheet()}
        {this.TopBar()}
        <Container>
          {this.state.installPrompt==false?"":<Paper elevation={4}>this.InstallPrompt()</Paper>}
          <Stack sx={{ mt: 5 }} direction="row" justifyContent="flex-start" alignItems="center" spacing={1.5}>
            {this.getTabSpecific({icon:true})}
            <h1>{this.getTabSpecific({title:true})}</h1>
          </Stack>
          <p>Version:0.0.6</p>
          {this.state.user?<p>You're signed in. Welcome, {this.state.user.displayName}!</p>:<p>You're not yet signed in.</p>}
          {this.state.tab==2 ? this.SignOut() : ""}
          {this.Content()}
          <p>{this.state.online ? "": (this.state.promptRetry?"You're not really connected to us 😕.\u00A0\u00A0":"Loading the posts for you...")}{this.state.promptRetry?<u style={{fontWeight:'500'}} onClick={()=>this.retrievePosts(false)}>Retry?</u>:""}</p>
          {this.Footer()}
        </Container>
        {this.BottomNav()}
        {this.CreatePostSheet()}
      </ThemeProvider>);
  }

  Content() {
    var content;
    if (this.state.tab==0) content=this.state.donations; else if (this.state.tab==1) content=this.state.requests; else content=this.state.userposts;
    if (this.state.online) return (content==undefined||content.length==0 ? <p>{this.state.tab==2?"You haven't posted yet. Your approved posts will show here.":"Sorry, there are no posts at the moment."}</p> : content?.map((datum) => this.ItemComponent(datum))); 
  }
  ItemComponent(item) {
    return (
      <Card elevation={4} sx={{ my: "0.8rem", py: "0.8rem", px: "1rem" }} onClick={() => this.changeShowItem(item)}>      
        <Stack direction="row" justifyContent="flex-start" alignItems="center" spacing={1.5} sx={{ mb:1 }}>
          {getCategoryIcon(item.c)}
          <p>{item.category}</p>
        </Stack>
        <Stack direction="row" justifyContent="flex-start" alignItems="center" spacing={1.5}>
          {Location(this.state.tab)}
          <p>{item.district}</p>
        </Stack>
        <p class="shortDescription">{item.d}</p>
        <Stack direction="row" justifyContent="flex-start" alignItems="center" spacing={1.5}>
          {Time()}
          <p>{Math.round((Date.now()-item.t)/86400000)} days ago</p>
        </Stack>
      </Card>);
  }

  ExpandedItem() {
    var item = this.state.openedItem;
    var contactOptions=0;
    if (item.e) contactOptions++; if (item.p&&!item.m) contactOptions++; if (item.p) contactOptions++;

    return (
      <ThemeProvider theme={this.getTabSpecific({theme:true})}>
        {this.SignInSheet(true)}
        {this.TopBar()}
        <Container sx={{ mt: 12 }}>
          {this.state.promptSignIn&&this.state.signedIn?<Alert severity="success" sx={{ width: '93%' }} style={{fontFamily:'Inter'}}><AlertTitle style={{fontFamily:'Inter', fontWeight:'600'}}>Signed in!</AlertTitle>Welcome! Feel free to contact this person now.</Alert>:""}
          {!this.state.signedIn?<Alert action={<Button color="inherit" size="small" style={{fontFamily:'Inter', fontWeight:'600'}} onClick={()=>this.changeShowItem(undefined)}>Sign In </Button>} severity="info" sx={{ width: '93%' }} style={{fontFamily:'Inter'}}>View all donations and requests in Bersamamu now!</Alert>:""}
          <Stack direction="column" justifyContent="flex-start" alignItems="center">
            <h1>{item.n}</h1>
            <Stack direction="row" justifyContent="flex-start" alignItems="center" spacing={1.5} sx={{ mb:1 }}>
              {getCategoryIcon(item.c)}
              <p>{item.category}</p>
            </Stack>
          </Stack>
          <br/><p>{item.d}</p><br/>
          <Stack direction="row" justifyContent="flex-start" alignItems="center" spacing={1.5}>
            {Location(this.state.tab)}
            <p>{item.l3 ? item.l3+", "+item.l2 : item.l2}</p>
          </Stack>
          <Stack direction="row" justifyContent="flex-start" alignItems="center" spacing={1.5}>
            {Location(this.state.tab)}
            <p>{item.district}</p>
          </Stack>
          <br/>
          <Stack direction="row" justifyContent="flex-start" alignItems="center" spacing={1.5}>
            {Time()}
            <p>{Math.round((Date.now()-item.t)/86400000)} days ago</p>
          </Stack>
          <br/>
          <br/>
          <br/>
          <br/>
        </Container>
        <Stack direction="row" justifyContent="center" alignItems="center">
          {item.e ? <Button sx={{ position: 'fixed', bottom: 0, width: 100/contactOptions+"%", left:0 }} style={{borderRadius: 0, backgroundColor: "#FFBFB3", color: "#444444", textTransform: 'none', fontFamily: "Inter", fontWeight: '500', fontSize: '1.2em' }} onClick={() => this.state.signedIn ? window.open("mailto:"+item.e): this.setState({promptSignIn:true})}>Email</Button> : ""}
          {item.p&&!item.m ? <Button sx={{ position: 'fixed', bottom: 0, width: 100/contactOptions+"%"}} style={{borderRadius: 0, backgroundColor: "#AEE4FF", color: "#444444", textTransform: 'none', fontFamily: "Inter", fontWeight: '500', fontSize: '1.2em' }} onClick={() => this.state.signedIn ? window.open("tel:+60"+item.p): this.setState({promptSignIn:true})}>Phone</Button> : ""}
          {item.p ? <Button sx={{ position: 'fixed', bottom: 0, width: 100/contactOptions+"%", right:0}} style={{borderRadius: 0, backgroundColor: "#BEFFB3", color: "#444444", textTransform: 'none', fontFamily: "Inter", fontWeight: '500', fontSize: '1.2em' }} onClick={() => this.state.signedIn ? window.open("https://wa.me/60"+item.p): this.setState({promptSignIn:true})}>WhatsApp</Button> : ""}
        </Stack>
      </ThemeProvider>
    );
  }

  SignInSheet(fromShare = false) {
    var title = fromShare ? "To contact this person, sign in now! 📲" : "Welcome to Bersamamu Web! 👋";
    var text = fromShare ? "You'll also be able to view other donations and requests in the Bersamamu platform." : "At Bersamamu, we aim to connect 🤝 kind people (like you!) to those in need, especially the numerous people affected by floods 🌊 and the pandemic 😷.\n\nYou can use this platform to ask for help 🏳 or provide help 🦸‍♂️ easily.\n\nSign in to start giving and receiving! ✨"
    return <React.Fragment>
      <Drawer anchor={'bottom'} open={(this.state.promptSignIn||!fromShare)&&!this.state.signedIn} onClose={()=>this.setState({promptSignIn:false})}>
        <h3 style={{marginLeft:'5vw', marginRight:'5vw', marginTop:'5vh'}}>{title}</h3>
        <p style={{marginLeft:'5vw', marginRight:'5vw', marginBottom:'3vh', whiteSpace: 'pre-line'}}>{text}</p>
        <a href="https://jing-yen.github.io/Charity-Privacy-Policy/" style={{marginLeft:'5vw', marginRight:'5vw', marginBottom:'5vh', fontWeight:'500', color:'black'}}>Check our privacy policy 📜</a>
        <Button style={{borderRadius: 12, marginLeft:'5vw', marginRight:'5vw', marginBottom:'5vh', paddingBottom:16, paddingTop:16, backgroundColor: "#FFF", color: "#000", textTransform: 'none', fontFamily: "Inter", fontWeight: '400', fontSize: '1.2em'}} sx={{boxShadow: 24, "& .css-9emuhu-MuiPaper-root-MuiDrawer-paper": { backgroundColor: "#eee" }}} variant="contained" onClick={() => this.firebase.signIn(this.onlineOrNot)}>{Google()}Sign in with Google</Button>
      </Drawer>
    </React.Fragment>
  }

  CreatePostSheet() {
    return <React.Fragment>
      <Drawer anchor={'bottom'} open={this.state.createPost!=2} onClose={()=>this.setState({createPost:2})}>
        <h3 style={{marginLeft:'5vw', marginRight:'5vw', marginTop:'5vh'}}>New {this.state.createPost==0?"donation":"request"}</h3>
        <p style={{marginLeft:'5vw', marginRight:'5vw', marginBottom:'3vh', whiteSpace: 'pre-line'}}>Migos</p>
        <a href="https://jing-yen.github.io/Charity-Privacy-Policy/" style={{marginLeft:'5vw', marginRight:'5vw', marginBottom:'5vh', fontWeight:'500', color:'black'}}>Check our privacy policy 📜</a>
        <Button style={{borderRadius: 12, marginLeft:'5vw', marginRight:'5vw', marginBottom:'5vh', paddingBottom:16, paddingTop:16, backgroundColor: "#FFF", color: "#000", textTransform: 'none', fontFamily: "Inter", fontWeight: '400', fontSize: '1.2em'}} sx={{boxShadow: 24, "& .css-9emuhu-MuiPaper-root-MuiDrawer-paper": { backgroundColor: "#eee" }}} variant="contained" onClick={() => this.firebase.signIn(this.onlineOrNot)}>{Google()}Sign in with Google</Button>
      </Drawer>
    </React.Fragment>
  }

  ShareLanding() {
    return (
      <div style={{position: 'fixed', width: '100%', height:'100%', textAlign:'center'}}>
        <img style={{height: this.state.splashSize+"vh", marginTop: (50-this.state.splashSize/2)+'vh'}} class="splash" src={logo}/>
        <h3 style={{marginTop: (18-this.state.splashSize/2)+'vh'}}>{this.state.online&&this.state.openedItem==undefined?"Sorry, this post is not available.":(this.state.promptRetry?"You're not really connected to us 😕.\u00A0\u00A0":"Checking for the shared item...")}{this.state.promptRetry?<u onClick={()=>this.retrievePosts(false)}>Retry?</u>:""}</h3>
        <p>Id: {window.location.hash.substring(7)}</p>
      </div>);
  }

  TopBar() {
    var bgColor;
    if (this.state.tab==0) bgColor="#8CC2FF"; else if (this.state.tab==1) bgColor="#FF9999"; else bgColor="#AAAAAA";
    return (
      <Stack position="static" sx={{ position: 'fixed', top: 0, width: 1 }} direction="column">
          <div style={{width: '100%', height: 10, backgroundColor: bgColor}}/>
          {this.shouldShowItem() ? this.ItemTopBar(this.state.tab) : ""}
      </Stack>);
  }
  ItemTopBar(tab) {
    return (
    <Paper sx={{ width: 1, borderRadius: 0 }} elevation={4}>
      <Stack direction="row" justifyContent="space-between" alignItems="center" sx={{ py: 1 }}>
        <a onClick={()=>this.changeShowItem(undefined)} style={{ paddingLeft: '1rem', paddingRight: '1rem' }}>{Back()}</a>
        <Stack direction="row" justifyContent="center" alignItems="center" spacing={2}>
          {tab==0 ? Donations() : (this.state.tab==1 ? Requests(): Me())}
          <h2>{tab==0 ? "Donations" : (this.state.tab==1 ? "Requests": "My Posts")}</h2>
        </Stack>
        <a onClick={this.share} style={{ paddingLeft: '1rem', paddingRight: '1rem' }}>{Share()}</a>
      </Stack>
    </Paper>);
  }
  InstallPrompt() {
    return (
      <Stack alignItems="center" direction="row" sx={{width: 1, backgroundColor:'white', px:2, py:2, display:'flex'}} spacing={2}>
      <a onClick={()=>this.setState({installPrompt:false})}>{Close()}</a>
      <Stack style={{flexGrow:0.9}} direction="column">
        <h3 style={{margin:0}}>More Bersamamu? 🥺</h3>
        <p style={{margin:0, marginTop:8}}>Add us to your home screen as a web app! It uses no storage.</p>
        </Stack>
        <Button style={{borderRadius: 8, paddingBottom:4, paddingTop:4, backgroundColor: "#C3E1FF", color: "#000", textTransform: 'none', fontFamily: "Inter", fontWeight: '400', fontSize: '1.1em'}} sx={{boxShadow: 8, "& .css-9emuhu-MuiPaper-root-MuiDrawer-paper": { backgroundColor: "#eee" }}} variant="contained" onClick={() => this.handleAppPromotion()}>Install</Button>
      </Stack>)
  }
  BottomNav() {
    return (
      <Stack direction="column" alignItems="flex-end" sx={{ position: 'fixed', bottom: 0, width: 1.0}}>  
      {this.state.tab==2?"":<Fab color="primary" sx={{mx:3,my:4}} onClick={()=>this.setState({createPost: this.state.tab})}><Add /></Fab>}
      <Paper sx={{ bottom: 0, width: 1.0, "& span, svg": {color: '#A0A0A0', fontFamily: 'Inter'} }} elevation={8}>
        <BottomNavigation showLabels value={this.state.tab} onChange={(event, newValue) => {this.setState({tab: newValue});}}>
          <BottomNavigationAction label="Items" fontWeight="2em" icon={Donations(this.state.tab==0, false)} sx={{"& .Mui-selected": { color: "#2850C9", fontFamily: 'Inter' }}}/>
          <BottomNavigationAction label="Requests" icon={Requests(this.state.tab==1, false)} sx={{"& .Mui-selected": { color: "#BD4242", fontFamily: 'Inter' }}}/>
          <BottomNavigationAction label="Me" icon={Me(this.state.tab==2, false)} sx={{"& .Mui-selected": { color: "#666666", fontFamily: 'Inter' }}}/>
        </BottomNavigation>
      </Paper></Stack>);
  }

  SignOut() {
    return (<div><br/><Button variant="contained" onClick={this.clearPosts} style={{backgroundColor: "#CCCCCC" }}>Sign Out</Button><br/><br/></div>)
  }

  Footer() { 
    return (<div><br/><br/><h6>2022 © Tong Jing Yen</h6><br/><br/><br/><br/></div>); 
  }

  render() {
    console.log(this.state.installPrompt);
    console.log(this.state.installPrompt==false);
    return (this.state.signedIn!=undefined||this.state.hashIsShare) && this.state.canContinue ? 
    (this.shouldShowItem() ? this.ExpandedItem() : (this.state.hashIsShare?this.ShareLanding():this.HomeBody())) : 
    (<div style={{position: 'fixed', width: '100%', height:'100%', textAlign:'center'}}><img style={{height: this.state.splashSize+"vh", marginTop: (50-this.state.splashSize/2)+'vh'}} class="splash" src={logo}/></div>);
  }
}

export default App;