import { initializeApp } from 'firebase/app';
import { getDatabase, ref, get, query, orderByChild, equalTo, startAt } from "firebase/database";
import { getAuth, signInWithPopup, GoogleAuthProvider, signOut } from "firebase/auth";
import Data from './data'

var firebaseConfig;
var app;
var auth;
var provider;

class Firebase {
    constructor() {
        firebaseConfig = {
            apiKey: "AIzaSyAUEH_i82BqLf9sAc_D72Xe_qqRv9FAK3g",
            authDomain: "charity-3ef3c.firebaseapp.com",
            databaseURL: "https://charity-3ef3c-default-rtdb.asia-southeast1.firebasedatabase.app",
            projectId: "charity-3ef3c",
            storageBucket: "charity-3ef3c.appspot.com",
            messagingSenderId: "398515566286",
            appId: "1:398515566286:web:bc291c2e1ba3d9b3e5c29f",
            measurementId: "G-RV61GX7N4V"
        };
        app = initializeApp(firebaseConfig);
        auth = getAuth();
        provider = new GoogleAuthProvider();
    }

    signIn(onlineOrNot) {
        // Reset online or not first.
        onlineOrNot(true);
        signInWithPopup(auth, provider)
            .then((result) => {
                onlineOrNot(true);
                // This gives you a Google Access Token. You can use it to access the Google API.
                const credential = GoogleAuthProvider.credentialFromResult(result);
                const token = credential.accessToken;
            })
            .catch((error) => {
                onlineOrNot(false);
                console.log("Bersamamu Web: Error "+error.code+" for "+error.email+": "+error.message);
            });
    }

    signOut() {
        signOut(auth).then(() => {
            // alert("Successful signout.");
        }).catch((error) => {
            console.error("Bersamamu Web: "+error);
        })
    }

    processData(snapshot, isdonation) {
        const data = new Data();
        let items = [];
        for (const [key, value] of Object.entries(snapshot.val())) {
            var newValue = value;
            const stateName = data.getStates()[parseInt(key.substring(0,2))];
            const districtName = data.getDistricts()[parseInt(key.substring(0,2))][value.l];
            const categoryName = data.getCategories()[value.c];
            const subCategoryName = data.getCategories()[value.c] ? data.getSubCategories()[value.c][value.c1] : undefined;
            newValue['district'] = districtName ? value.l1+" "+districtName+", "+stateName : value.l1+" "+stateName;
            newValue['category'] = categoryName ? (subCategoryName ? subCategoryName+" • "+categoryName : "Others • "+categoryName) : "Others";
            newValue['isdonation'] = isdonation;
            newValue['key'] = key;
            items.push(newValue);
        }
        return items.sort(function(a,b) {return a['t']-b['t']});
    }

    async obtainDonations(onlineOrNot) {
        onlineOrNot(false); 
        const dbRef = ref(getDatabase(), 'items');
        var snapshot = await get(query(dbRef, orderByChild("t"), startAt(Date.now()-2419200000))).catch((error)=>console.error(error));
        if (snapshot.exists()) { onlineOrNot(true); return this.processData(snapshot, true).reverse(); } 
        else { console.error("no items"); return [] }
    }

    async obtainRequests(onlineOrNot) {
        onlineOrNot(false);
        const dbRef = ref(getDatabase(), 'requests');
        var snapshot = await get(query(dbRef, orderByChild("t"), startAt(Date.now()-1209600000))).catch((error)=>console.error(error));
        if (snapshot.exists()) { onlineOrNot(true); return this.processData(snapshot, false).reverse(); } 
        else { console.error("no items"); return [] }
    }

    async obtainUserPosts(uid, onlineOrNot) {
        onlineOrNot(false); 
        let items = [];
        const dbRef = ref(getDatabase(), 'items');
        var snapshot = await get(query(dbRef, orderByChild("u"), equalTo(uid))).catch((error)=>console.error(error));
        if (snapshot.exists()) {
            onlineOrNot(true); items = this.processData(snapshot, true).filter(function (a) {return Date.now()-a['t']<=2419200000});
        } else { console.error("no items") }
        const dbRef2 = ref(getDatabase(), 'requests');
        var snapshot2 = await get(query(dbRef2, orderByChild("u"), equalTo(uid))).catch((error)=>console.error(error));
        if (snapshot2.exists()) {
            items = items.concat(this.processData(snapshot, false).filter(function (a) {return Date.now()-a['t']<=1209600000}));
        } else { console.error("no items") }
        return items;
    }
}

export default Firebase;